import React, { useEffect } from "react";
import { IonLoading } from '@ionic/react';
import * as Constants from 'app/utils/constants';
import { createStructuredSelector } from "reselect";
import { getLoading } from "./selectors/selector";

import useInjectSaga from "app/utils/injectSaga";
import useInjectReducer from "app/utils/injectReducer";
import { connect } from "react-redux";
import { compose } from "redux";
import { Plugins } from '@capacitor/core';

import reducer from "app/reducers/App/reducer";
import saga from "app/saga/App/saga";

import RootLevelTabs from 'app/components/app/Bars/Bar-bottom'
import { useWatchPosition, availableFeatures, useCurrentPosition } from '@ionic/react-hooks/geolocation';
import { useAppState } from '@ionic/react-hooks/app/useApp'
import { updateGpsCoordinates } from './actions/App/action';
//import { getGPSDetails } from 'app/selectors/selector';
const { Geolocation } = Plugins;
const key = "App";
const App: React.FC = (props: any) => {

  const { loading } = props;
  //const { currentPosition: watchPosition, error, getPosition } = useCurrentPosition();
  let { currentPosition: watchPosition, startWatch, clearWatch } = useWatchPosition();
  const { isAvailable, state: appState } = useAppState();
  
  function getCurrentPosition() {
    Geolocation.getCurrentPosition().then((result) => {
      var now = new Date();
      const lat = result.coords.latitude;
      const long = result.coords.longitude;
      const gpsObj = { latitude: lat, longitude: long, timestamp: now.getHours()+":"+now.getMinutes()+":"+now.getSeconds() };
      props.updateGpsCoordinates(gpsObj);
    })
  }
  useEffect(() => {
    //startWatch({ enableHighAccuracy: true, timeout: 10000, maximumAge: 0 })
    getCurrentPosition();
    setInterval(() => {
      getCurrentPosition();
    }, 10000)

  }, [])

  // useEffect(() => {

  //   if (!appState) {
  //     console.log('Run in background');
  //   }
  //   var now = new Date();
  //   if(lastUpdateTime && now.getTime() - lastUpdateTime.getTime() < minFrequency){
  //       return;
  //   }
  //   lastUpdateTime = now;

  //   const coordinates = watchPosition && watchPosition.coords;
  //   console.log(coordinates)
  //   if (coordinates) {
  //     const { latitude, longitude } = coordinates;

  //     const gpsObj = { latitude, longitude, timestamp: new Date() };
  //     console.log("GPS VALUES:", gpsObj);
  //     props.updateGpsCoordinates(gpsObj);
  //   }
  // }, [watchPosition])

  return (
    <React.Fragment>
      <IonLoading
        isOpen={loading}
        message={'loading...'}
        show-backdrop={false}
        spinner="circles"
      />
      <RootLevelTabs />
    </React.Fragment>
  );
};

const mapStateToProps = createStructuredSelector({
  loading: getLoading(),
  // gpsData: getGPSDetails()

})
const mapDispatchToProps = (dispatch: Function) => ({
  updateGpsCoordinates: (data: any) => dispatch(updateGpsCoordinates(data))
});


const withConnect = connect(
  mapStateToProps,
  mapDispatchToProps
);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose(
  withSaga,
  withReducer,
  withConnect
)(App);